
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';


@Injectable()
export class NavigatorService{
  private readonly apiEndpoint = 'blocknet/xrouter';
  private baseEndpoint = ''; // http://localhost

  constructor(private router: Router) {}

  go(path: string) {
    this.router.navigate([path]);
  }

  serviceNodeDetails(address: string) {
    this.go('/xrouter-snodes/' + address);
  }

  spvWalletDetails(blockhash: string) {
    this.go('/spv-wallets/' + blockhash);
  }

  xCloudServiceDetails(txid: string) {
    this.go('/xcloud-services/' + txid);
  }

  

}
